import React from 'react';
import { Row, Col } from 'reactstrap';
import PopUp from './Pop_up';
import MetaTags from 'react-meta-tags';
class Como_funciona extends React.Component{
  componentDidMount() {
    window.scrollTo(0, 0)
  }
  render(){
    return(
    <Row>
      <MetaTags>
        <title>¿Cómo funciona Superé? | Superé Envigado</title>
        <meta property="og:title" content="¿Cómo funciona Superé?" />
      </MetaTags>
     <Col xs="12" className='tittle'><h2 className='tittleh'>Información sobre crédito condonable y porcentajes</h2></Col>
     <Col xs="12" className='tittle'><div className='sublineP' div></div></Col>
     <Col md="7" className="marginRow justify">
      <h5 className="bold">Porcentajes de cubrimiento</h5>
      <p>El estímulo consiste en un apoyo económico que se le brinda al estudiante, cubriéndole una parte o porcentaje de su matrícula semestral. No se ofrece un cubrimiento del 100%, en todos los casos el estudiante debe contribuir con el porcentaje restante para completar el valor total de la matrícula.</p>
      <p>Los porcentajes de cubrimiento del programa varían de acuerdo con la naturaleza de la Institución Educativa de la cual se graduó el estudiante y la naturaleza de la universidad a la que desee ingresar.</p>
      <p>En este orden de ideas, se presentan los siguientes escenarios: </p>
     </Col>
     <Col md="5" className="marginRow"><img src="https://cidpullzonestorage.b-cdn.net/supere/2PORCENTAJESfx.png" alt="" /></Col>
     <Col md ="7" className="marginRow">
      <PopUp image='https://cidpullzonestorage.b-cdn.net/supere/porcentajesfx.png'/>
     </Col>
     <Col md ="5" className="marginRow d-flex align-items-center">
      <div>
        <div>
          <h5 className="bold">Graduado de I.E. Oficial:</h5>
          <p><span className="bold">U. Pública:</span> Envigado cubre el 90% de la matrícula y el estudiante cubre el 10% restante. </p>
          <p><span className="bold">U. Privada:</span> Envigado cubre el 95% de la matrícula y el estudiante cubre el 5% restante. </p>
        </div>
        <div>
          <h5 className="bold">Graduado de colegio privado:</h5>
          <p><span className="bold">U. Pública:</span> Envigado cubre el 75% de la matrícula y el estudiante cubre el 25% restante. </p>
          <p><span className="bold">U. Privada:</span> Envigado cubre el 75% de la matrícula y el estudiante cubre el 25% restante.</p>
        </div>
      </div>
     </Col>
     <Col md="6" className="marginRow d-flex align-items-center">
      <div>
        <p>
         <h5 className="bold">Excepciones:</h5>
         <p>Las siguientes universidades no siguen las reglas descritas, por lo que el porcentaje de cubrimiento no depende de la naturaleza del colegio ni de la Universidad:
         </p>
        </p>
        <p>
          <p>
          <h6 className="bold_green">EIA – UPB: </h6>
           <p>Envigado cubre el 75% de la matricula y el estudiante cubre el 25% restante.</p>
          </p>
          <p>
          <h6 className="bold_green">CES: </h6>
           <p>Envigado cubre 60% de la matricula y el estudiante cubre el 40% restante.</p>
          </p>
          <p>
          <h6 className="bold_green">CUL: </h6>
           <p>Envigado cubre desde el 58% de la matricula y el estudiante cubre hasta el 42% restante. </p>
          </p>
        </p>
      </div>
     </Col>
     <Col md="6" className="marginRow"><PopUp image='https://cidpullzonestorage.b-cdn.net/supere/excepciones.png'/></Col>
     <Col md="4" className="marginRow marginRow_bottom d-flex align-items-center"><img src="https://cidpullzonestorage.b-cdn.net/supere/3CREDITO.png" alt="" /></Col>
     <Col md="8" className="marginRow marginRow_bottom">
       <h5 className="bold">Crédito condonable</h5>
       <p>La modalidad de los estímulos ofrecidos por Superé es conocida como crédito condonable. En primera instancia, los recursos cubiertos por el municipio, se entregan como préstamo al ciudadano, por lo que es necesario un codeudor y la firma de un pagaré. </p>
       <p>El municipio establece cuatro condiciones para sus estudiantes, y estos las deben cumplir con el fin de condonar el crédito. Siempre y cuando se cumplan dichas condiciones a lo largo de la carrera, entonces el beneficiado no realiza el pago del crédito. </p>
       <p>Las siguientes son las condiciones de condonación: </p>
       <p>
         <ul className="list-fixed">

           <li><span>Sostener un promedio semestral superior a 3,5. </span></li>
           <li><span>Realizar un número determinado de horas sociales semestrales.</span></li>
           <li><span>Obtener el titulo universitario en el número de semestres indicado en el pensum académico. </span></li>
           <li><span>Culminar la carrera y obtener el título profesional o tecnológico según el caso. </span></li>
         </ul>
       </p>
     </Col>
    </Row>
      )
  }
}

export default Como_funciona;



