import React from 'react';
import {  Row, Col } from 'reactstrap';
import { Link } from 'react-router-dom';
import pata1 from '../images/pata-1.jpg';
import pata2 from '../images/pata-2.jpg';
const Footer = () =>{
  return(
  <div>
    <div className="footer">
      <Row>
       <Col md="3">
       <p className="text_decreto center"><img src="https://cidpullzonestorage.b-cdn.net/supere/logo_positivo.png" alt=""/></p>
       <p className="center white text_decreto"><a href="https://cidpullzonestorage.b-cdn.net/supere/pdf/decreto_reglamentario.pdf" target="_blank" rel="noopener noreferrer">CONOCE EL DECRETO REGLAMENTARIO</a></p>
       </Col>
       <Col md="3">
        <div className="text_footer">
          <ul>
              <li><Link to="/">INICIO</Link></li>
              <li><Link to="/Como_funciona">¿CÓMO FUNCIONA?</Link></li>
              <li><Link to="/Donde_estudiar">QUÉ Y DÓNDE ESTUDIAR</Link></li>
              <li><Link to="/Requisitos">REQUISITOS</Link></li>
              <li><Link to="/Preguntas">PREGUNTAS</Link></li>
              <li><Link to="/Inscribite">INSCRÍBETE</Link></li>
            </ul>
        </div>
       </Col>
       <Col md="4">
        <div className="text_footer lower">
            <ul>
              <li><i className="fa fa-map-marker"></i>CALLE 37 SUR #45B-27, LOCAL 3 ENVIGADO, ANTIOQUIA</li>
              <li><i className="fa fa-envelope"></i>INFO@SUPERE-ENVIGADO.ORG</li>
              <li><i className="fa fa-phone"></i>3394000 EXT 4814</li>
            </ul>
        </div>
       </Col>
       <Col md="2">
        <div className="text_footer">
            <ul>
              <li><a href="https://www.instagram.com/supere_envigado" target="_blank" rel="noopener noreferrer"><i className="fa fa-instagram"></i>/Superé Envigado</a></li>
              <li><a href="https://www.facebook.com/Superenvigado/" target="_blank" rel="noopener noreferrer"><i className="fa fa-facebook"></i>/supere_envigado</a></li>
              <li><a href="https://www.youtube.com/channel/UCVnelDk69jfK9Lf-8L6H4KQ" target="_blank" rel="noopener noreferrer"><i className="fa fa-youtube"></i>/Superenvigado</a></li>
            </ul>
        </div>
        </Col>
      </Row>
      </div>
        <div className="m-grid__item m-footer">
          <img src={pata1} alt=""/>
          <img src={pata2} alt=""/>
        </div>
      </div>
    )
}

export default Footer;



