import React from 'react';
import { Button, Container, Row, Col } from 'reactstrap';
import MetaTags from 'react-meta-tags';
class Donde_estudiar extends React.Component{
  componentDidMount() {
    window.scrollTo(0, 0)
  }
  render(){
    return(
    <div className="cotenido_general">
      <MetaTags>
        <title>¿Qué y dónde estudiar con Superé? | Superé Envigado</title>
        <meta property="og:title" content="¿Qué y dónde estudiar con Superé?" />
      </MetaTags>
      <Container>
        <Row className='marginRow_bottom'>
          <Col xs="12" className='tittle'><h2 className='tittleh'>Universidades</h2></Col>
          <Col xs="12" className='tittle'><div className='sublineP' div></div></Col>
          <Col xs="12" className='marginRow'>
          <p className='bold center'>
            En superé tenemos 14 universidades aliadas en las que puedes iniciar tus estudios de educación superior.
          </p>
          </Col>
          <Col xs="12" md='6' className='marginTB'>
            <div className='imgB'>
              <img src='https://cidpullzonestorage.b-cdn.net/supere/universidades.jpg' alt='' />
              <p>Haz clic en el boton para conocer los programas a los que puedes acceder</p>
            </div>
            <div className='marginRow'>
              <p className="center"><Button color="primary"><a href="https://cidpullzonestorage.b-cdn.net/supere/pdf/universidades.pdf" target="_blank" rel="noopener noreferrer">PROGRAMAS</a></Button></p>
            </div>
          </Col>
          <Col xs="12" md='6' className='marginTB'>
            <ul className='bbold list-fixed'>
              <li>
                <span>Universidad EAFIT</span>
              </li>
              <li>
                <span>ITM (Instituto Tecnológico Metropolitano)</span>
              </li>
              <li>
                <span>Escuela de Ingenieros de Antioquia </span>
              </li>
              <li>
                <span>Institución Universitaria de Envigado</span>
              </li>
              <li>
                <span>Universidad de Medellín</span>
              </li>
              <li>
                <span>Escuela superior tecnológica de artes Débora Arango</span>
              </li>
              <li>
                <span>Universidad Cooperativa de Colombia</span>
              </li>
              <li>
                <span>Tecnológico de Antioquia</span>
              </li>
              <li>
                <span>Institución Universitaria Pascual Bravo</span>
              </li>
              <li>
                <span>Politécnico Jaime Isaza Cadavid</span>
              </li>
              <li>
                <span>Universidad Pontificia Bolivariana</span>
              </li>
              <li>
                <span>Universidad CEIPA</span>
              </li>
              <li>
                <span>Universidad CES</span>
              </li>
              <li>
                <span>Corporación Universitaria Lasallista</span>
              </li>
            </ul>
          </Col>
        </Row>
      </Container>
      </div>
      )
  }
}

export default Donde_estudiar;



