import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import {
    Accordion,
    AccordionItem,
    AccordionItemTitle,
    AccordionItemBody,
} from 'react-accessible-accordion';

// Demo styles, see 'Styles' section below for some notes on use.
import 'react-accessible-accordion/dist/fancy-example.css';
import MetaTags from 'react-meta-tags';


class Preguntas extends React.Component{
  componentDidMount() {
    window.scrollTo(0, 0)
  }
  render(){
    return(
     <div className="cotenido_general">
       <MetaTags>
         <title>Preguntas frecuentes sobre Superé| Superé Envigado</title>
         <meta property="og:title" content="Preguntas frecuentes sobre Superé" />
       </MetaTags>
        <Container>
          <Row>
            <Col xs="12" className='tittle'><h2 className='tittleh'>Preguntas frecuentes</h2></Col>
            <Col xs="12" className='tittle'><div className='sublineP' div></div></Col>
            <Col md="8">

           <Accordion className="marginRow marginRow_bottom">
          <AccordionItem>
              <AccordionItemTitle>
                  <h5 className="bold">¿Superé es una beca?</h5>
              </AccordionItemTitle>
              <AccordionItemBody>
            <Col md='12' className='marginTB'>
              <p className='justify'>
              No, Superé es un estímulo a la educación superior, que consiste en un apoyo
              económico que se les entrega a los beneficiarios, a través del cual se les cubre un
              porcentaje o una parte de la matricula semestral.
              </p>
            </Col>
              </AccordionItemBody>
          </AccordionItem>

           <AccordionItem>
              <AccordionItemTitle>
                  <h5 className="bold">¿Qué es un crédito condonable?</h5>
              </AccordionItemTitle>
              <AccordionItemBody>
                 <Col md="12"  className='marginTB'>
              <p className='justify'>
              Un crédito condonable es como te financiaremos un porcentaje de tu matricula,
                siempre y cuando culmines la carrera que escojas, obtengas el título profesional y
                cumplas con los requisitos de condonación, no tendrás que pagarle al Municipio los
                desembolsos hechos.
              </p>
            </Col>
              </AccordionItemBody>
          </AccordionItem>

           <AccordionItem>
              <AccordionItemTitle>
                  <h5 className="bold">¿Qué hacer sino apareces como preseleccionado?</h5>
              </AccordionItemTitle>
              <AccordionItemBody>
                   <Col md='12' className='marginTB'>
              <p className='justify'>
              No te desanimes, sino resultas preseleccionado puedes buscar otras opciones de
              financiación, o puedes esperar a una próxima convocatoria de Superé para volver a
              intentarlo, siempre y cuando no hayas iniciado ningún semestre académico.
              </p>
            </Col>
              </AccordionItemBody>
          </AccordionItem>

           <AccordionItem>
              <AccordionItemTitle>
                  <h5 className="bold">¿Los beneficiarios pueden cambiar de programa o de institución de
            educación superior?</h5>
              </AccordionItemTitle>
              <AccordionItemBody>
                  <Col md='12' className='marginTB'>
              <p className='justify'>
              No, el programa Superé adjudica sus créditos condonables para una carrera y universidad específica, por lo que no es posible realizar ningún tipo de cambio.
              </p>
            </Col>
              </AccordionItemBody>
          </AccordionItem>

           <AccordionItem>
              <AccordionItemTitle>
                  <h5 className="bold">¿Qué promedio exige el programa para los beneficiarios?</h5>
              </AccordionItemTitle>
              <AccordionItemBody>
                 <Col md='12' className='marginTB'>
              <p className='justify'>
              El promedio exigido por el programa para la permanencia es mínimo de 3,5 para el promedio semestral.
              </p>
            </Col>
              </AccordionItemBody>
          </AccordionItem>

           <AccordionItem>
              <AccordionItemTitle>
                  <h5 className="bold">¿Qué pasa si el beneficiario no cumple con los requisitos para condonar el crédito?</h5>
              </AccordionItemTitle>
              <AccordionItemBody>
                  <Col md='12' className='marginTB'>
              <p className='justify'>En caso de que un beneficiario del programa no cumpla con el requisito de condonación o pierda su condición de beneficiario, deberá pagar al municipio el total del valor por concepto de matrículas desembolsadas hasta la fecha.
              </p>
            </Col>
              </AccordionItemBody>
          </AccordionItem>

      </Accordion>
            </Col>
            <Col md="4" className="marginRow marginRow_bottom">
            <img src="https://cidpullzonestorage.b-cdn.net/supere/IMG-Preguntasfrecuentes.png" alt=""/>
            </Col>
          </Row>

        </Container>
      </div>
  );
  }
}

export default Preguntas;



