import React from 'react';
import { Container, Row, Col, Button } from 'reactstrap';
import MetaTags from 'react-meta-tags';

class Inscribite extends React.Component{
  componentDidMount() {
    window.scrollTo(0, 0)
  }
  render(){
  return(
    <div className="cotenido_general">
      <MetaTags>
        <title>Inscríbete en Superé | Superé Envigado</title>
        <meta property="og:title" content="Inscríbete en Superé" />
      </MetaTags>
      <Container>
        <Row className='marginRow_bottom'>
          <Col xs="12" className='tittle'><h2 className='tittleh'>Proceso de inscripción</h2></Col>
          <Col xs="12" className='tittle'><div className='sublineP' div></div></Col>

          <Col xs="12" md='8' className='marginRow'>
            <p>
              Para quedar correctamente inscrito en el programa Superé, debes
              seguir los siguientes pasos:
            </p>
            <ul className='list-fixed'>
              <li>
                <span>
                  Crea una cuenta en nuestra plataforma, siguiendo el proceso
                  de registro, recuerda colocar una contraseña que recuerdes
                  con facilidad.
                </span>
              </li>
              <li><span>
                Activa la cuenta a través del correo que recibiste en tu
                bandeja de entrada.
              </span></li>
              <li><span>
                Ingresa a nuestra plataforma y diligencias los 5 formularios
                de información.
              </span></li>
              <li><span>
                Adjunta de manera escaneada los 5 documentos que te
                solicitamos.
              </span></li>
              <li><span>
                Selecciona tus tres opciones de carrera en orden de gusto e
                importancia para ti.
              </span></li>
            </ul>
            <p className="center marginRow"><a href="http://inscripcion.supere-envigado.org" target="_blank" rel="noopener noreferrer"><Button color="primary">INSCRIBETE AQUÍ</Button></a></p>
          </Col>
          <Col xs="12" md='4' className='marginRow'>
            <div className='imgB'>
              <img src='https://cidpullzonestorage.b-cdn.net/supere/IMG-Procesodeinscripcion.png' alt=""/>
            </div>
          </Col>
          <Col xs="12" className='tittle'><h2 className='tittleh'>Procesos legales</h2></Col>
          <Col xs="12" className='tittle'><div className='sublineP' div></div></Col>
          <Col xs="12" className='tittle marginRow bluetext center'>
            <p>
              Una vez eres beneficiario del programa Superé, debes conocer los procesos legales que deben
              llevarse a cabo para formalizar el beneficio semestre a semestre.
            </p>
          </Col>
          <Col xs="12" className='marginRow'><h4 className='tittleS '>Legalización</h4></Col>
          <Col xs="12" className='marginTB'>
            <p>
              La legalización, es la adjudicación del beneficio, consiste en la formalización de este a través de la firma de dos documentos legales (pagaré y carta de instrucciones), este proceso se realiza una única vez.
            </p>
            <p>
              Para legalizar son necesarios una serie de documentos que debes entregar de forma física en la oficina de Superé en las fechas en que seas
              citado, estos son los documentos:
            </p>
          </Col>

          <Col xs="12" md='6' className='marginTB'>
            <ul className='list-fixed'>
              <li>
                <p>
                  Copia del documento de identidad de la estudiante ampliada
                  al 150.
                </p>
              </li>
              <li><p>
                Copia del documento de identidad del Deudor ampliada al
                150.
              </p></li>
            <li><p>
                Certificado o carta de admisión expedido por la Universidad
                con la siguiente información: nombre del pregrado y Valor
                total de la matrícula.
              </p></li>
            <li><p>
                Certificado de promedio académico del grado once expedido y
                firmado por la Institución educativa o colegio.
              </p></li>
            <li><p>
                Impresión de los resultados de las pruebas SABER 11- ICFES.
              </p></li>
            <li><p>
                Certificado del puntaje del SISBEN, expedido por la Oficina del
                SISBEN de Envigado, no del DNP (Pueden solicitarlo en la
                  oficina ubicada al frente del MUA hasta las 11:00 a.m.)
                </p></li>
              <li><p>
                  Certificado de residencia a nombre del beneficiario (pueden
                    solicitarlo en la inspección de policía del parque, deben ir con
                    documento original, copia de los servicios y certificado del
                    SISBEN)
                  </p></li>
                <li><p>
                    Copia de la cuenta de servicios (EPM).
                  </p></li>

                </ul>
              </Col>
              <Col xs="12" md='6' className='marginTB'>
                <ul className='list-fixed'>
              <li><p>
                    Declaración de renta y/o certificado de ingresos y retenciones
                    del año gravable inmediatamente anterior del deudor
                    solidario.
                  </p></li>
                <li><p>
                    Declaración de renta y/o certificado de ingresos y retenciones
                    del año gravable inmediatamente anterior del beneficiarioSOLO
                    PARA MAYORES DE EDAD.
                  </p></li>
                <li><p>
                    Para el caso de trabajadores independientes, certificado de
                    ingresos del deudor solidario expedido por el contador
                    público.
                  </p></li>
                <li><p>
                    Además, para la legalización necesitas de un codeudor quien
                    amparará tu compromiso económico con el Municipio, este
                    debe cumplir con las quienes condiciones:
                  </p></li>
                <li><p>
                    Mayor de edad y menor de 65 años.
                  </p></li>
                <li><p>
                    Debe tener un ingreso mensual certificable superior a 1
                    SMMLV.
                  </p></li>
                <li><p>
                    No debe ser pensionado.
                  </p></li>
                </ul>
              </Col>
              <Col xs="12" className='marginRow'><h4 className='tittleS '>Renovación</h4></Col>
              <Col xs="12" className='marginTB'>
                <p>
                  La renovación es el procedimiento a través del cual verificamos que los estudiantes cumplan con las condiciones de permanencia, este
proceso se lleva a cabo semestralmente, deben presentar en la oficina de Superé los siguientes documentos:
                </p>
              </Col>
              <Col xs="12" md='6' className='marginRow'>
                <div className='imgBsmall'>
                  <img src='https://cidpullzonestorage.b-cdn.net/supere/renovacionfx.png' alt=""/>
                </div>
              </Col>
              <Col xs="12" md='6' className='marginRow d-flex align-items-center'>
                <ul className='list-fixed'>
                  <li>
                    <p>
                      Certificado de notas y promedio semestral, donde se
evidencie cada asignatura con su respectiva nota.
                    </p>
                  </li>
                  <li><p>
                    Certificado, constancia de matricula o liquidación donde se
indique el valor de la matricula para el semestre siguiente.
                  </p></li>
                <li><p>
                  Certificado y formatos de prestación de servicio social
debidamente diligenciado y firmado.
                  </p></li>
                <li><p>
                    Comprobante de pago al Municipio.
                  </p></li>

                    </ul>
                  </Col>
            </Row>
          </Container>
        </div>
      )}
    }

    export default Inscribite;



