import React from 'react';
import {  Row, Col, Button, TabContent, TabPane, Nav, NavItem, NavLink} from 'reactstrap';
import classnames from 'classnames';
import MetaTags from 'react-meta-tags';
import scrollToComponent from 'react-scroll-to-component';
class Home extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      activeTab: '1'
    };
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }
  componentDidMount() {
    window.scrollTo(0, 0)
  }
  render() {
    return (
     <Row>
       <MetaTags>
         <title>Superé Envigado</title>
         <meta property="og:title" content="Superé Envigado" />
       </MetaTags>
      <Col xs="12" className='tittle'><h2 className='tittleh'>¿Qué es superé?</h2></Col>
      <Col xs="12" className='tittle'><div className='sublineP' div></div></Col>
      <Col xs="12" md="8" className="marginRow justify">
       <p>Superé es el nombre que define el programa de la Administración Municipal, a través del cual
          se ofrece a los estudiantes del Municipio de Envigado una opción para que puedan acceder a
          la educación superior.</p>
       <p>El Alcalde Raúl Cardona González, planteó desde su propuesta programática y definió en el
          Plan de Desarrollo Municipal, la necesidad de encontrar estrategias que permitan a jóvenes de
          Envigado el acceso y la continuidad en sus procesos de educación superior. “La gratuidad en la
          educación básica y secundaria es un logro muy importante en Envigado, pero se queda corto
          si no podemos garantizar a esos estudiantes puedan tener acceso a una carrera técnica,
          tecnológica o universitaria. Se calcula que aproximadamente el 65% de los estudiantes que
          terminan su bachillerato no tienen como continuar con su educación superior”, señaló el
          Alcalde de la Ciudad Señorial.</p>
      </Col>
      <Col md="3" className="marginRow centerIm d-flex align-items-center"><img src="https://cidpullzonestorage.b-cdn.net/supere/1LOGOSUPERE.png" alt=""/></Col>
      <Col md="12" className="center marginRow"> <a rel="noopener noreferrer" href="https://cidpullzonestorage.b-cdn.net/supere/pdf/decreto_reglamentario.pdf" target="_blank"><Button color="primary">DESCARGA DECRETO REGLAMENTARIO</Button></a></Col>
      <Col xs="12" className='tittle'><h2 className='tittleh'>Programas complementarios</h2></Col>
      <Col xs="12" className='tittle marginRow_bottom'><div className='sublineP' div></div></Col>
   <Nav tabs>
   <Col  md="1"></Col>
        <Col xs="6" md="2" onClick={() => {scrollToComponent(this.Violet, { offset: 0, align: 'top', duration: 1500}) }}>
          <NavItem className="center">
            <NavLink className={classnames({ active: this.state.activeTab === '1' })} onClick={() => { this.toggle('1'); }}>
             <img src="https://cidpullzonestorage.b-cdn.net/supere/iconos/ConsultorioMatematico.png" alt=""/>CONSULTORIO MATEMÁTICO
            </NavLink>
          </NavItem>
        </Col>
         <Col xs="6" md="2" onClick={() => {scrollToComponent(this.Violet, { offset: 0, align: 'top', duration: 1500}) }}>
          <NavItem className="center">
            <NavLink className={classnames({ active: this.state.activeTab === '2' })} onClick={() => { this.toggle('2'); }}>
             <img src="https://cidpullzonestorage.b-cdn.net/supere/iconos/OrientacionVocacional.png" alt=""/>ORIENTACIÓN VOCACIONAL
            </NavLink>
          </NavItem>
        </Col>
         <Col xs="6" md="2" onClick={() => {scrollToComponent(this.Violet, { offset: 0, align: 'top', duration: 1500}) }}>
          <NavItem className="center">
            <NavLink className={classnames({ active: this.state.activeTab === '3' })} onClick={() => { this.toggle('3'); }}>
             <img src="https://cidpullzonestorage.b-cdn.net/supere/iconos/ServicioSocial.png" alt=""/>SERVICIO SOCIAL
            </NavLink>
          </NavItem>
        </Col>
         <Col xs="6" md="2" onClick={() => {scrollToComponent(this.Violet, { offset: 0, align: 'top', duration: 1500}) }}>
          <NavItem className="center">
            <NavLink className={classnames({ active: this.state.activeTab === '4' })} onClick={() => { this.toggle('4'); }}>
             <img src="https://cidpullzonestorage.b-cdn.net/supere/iconos/AcompanamientoPsicosocial.png" alt=""/>ACOMPAÑAMIENTO PSICOSOCIAL
            </NavLink>
          </NavItem>
        </Col>
         <Col xs="6" md="2" onClick={() => {scrollToComponent(this.Violet, { offset: 0, align: 'top', duration: 1500}) }}>
          <NavItem className="center">
            <NavLink className={classnames({ active: this.state.activeTab === '5' })} onClick={() => { this.toggle('5'); }}>
             <img src="https://cidpullzonestorage.b-cdn.net/supere/iconos/InglesSupere.png" alt=""/>INGLÉS SUPERÉ
            </NavLink>
          </NavItem>
        </Col>
        </Nav>
        <TabContent activeTab={this.state.activeTab} ref={(TabContent) => { this.Violet = TabContent; }}>
          <TabPane tabId="1">
             <Row>
             <Col md="6" className="marginRow justify marginRow_bottom" >
                <h5 className="bold">CONSULTORIO MATEMÁTICO</h5>
                <p>Este consultorio es un espacio creado especialmente para los beneficiarios del programa Superé, en donde pueden acceder a asesorías personalizadas con una docente experta en el área, quien los acompañará a lo largo del semestre. Este servicio es completamente gratuito. </p>

                <p>Horario: Para el semestre 2018 – 2, este es el horario en el que la docente estará disponible para atender a los estudiantes. </p>
                <p>
                  <ul className="list-fixed">
                  <li><span>Lunes: 10:00 a.m. a 12:00 m y 1:00 p.m. a 6:00 p.m.</span></li>
                  <li><span>Martes: 8:00 a.m. a 12:00 m.</span></li>
                  <li><span>Miércoles: 9:00 a.m. a 12:00 m y 1:00 p.m. a 6:00 p.m.</span></li>

                  </ul>
                </p>
                <p>Temas: El consultorio matemático atiende todo lo relacionado con el área, matemáticas básicas, cálculo, ecuaciones diferenciales y geometría. </p>
                <p>Lugar: el consultorio matemático se realiza en la oficina del programa, ubicada en la Biblioteca Débora Arango, Local 3. </p>
                </Col>
                <Col md="6" className="marginRow marginRow_bottom"><img src="https://cidpullzonestorage.b-cdn.net/supere/consultorio.jpg" alt=""/></Col>
             </Row>
          </TabPane>
        </TabContent>

           <TabContent activeTab={this.state.activeTab}>
          <TabPane tabId="2">
             <Row>
             <Col md="6" className="marginRow justify marginRow_bottom">
                <h5 className="bold">ORIENTACIÓN VOCACIONAL</h5>
                <p>El programa ofrece un servicio de acompañamiento vocacional para que los estudiantes tengan conocimiento de sus habilidades, fortalezas e intereses y así puedan realizar una elección de carrera más acertada. </p>
                <p>Este servicio consta de mínimo 3 citas con nuestro psicólogo; en donde además se presentarán una serie de test psicotécnicos.</p>
                <p>El acompañamiento vocacional es completamente gratuito para toda la comunidad envigadeña, sin importar su ingreso al programa Superé.</p>
                <p>Si deseas acceder a este servicio, debes enviar un correo a <b>superenvigado@gmail.com</b>, solicitando una cita para tu primera
                  sesión, en donde especifiques los siguientes datos, la cita te será asignada y recibirás una confirmación a través del correo.</p>
                <p>
                  <ul className="list-fixed">
                  <li><span>Nombre Completo</span></li>
                  <li><span>Carrera</span></li>
                  <li><span>Universidad</span></li>
                  <li><span>Motivo de consulta</span></li>
                   <li><span>Horario de disponibilidad</span></li>
                  </ul>
                </p>
                </Col>
                <Col md="6" className="marginRow marginRow_bottom"><img src="https://cidpullzonestorage.b-cdn.net/supere/orietacion.jpg" alt=""/></Col>
             </Row>
          </TabPane>
        </TabContent>

           <TabContent activeTab={this.state.activeTab}>
          <TabPane tabId="3">
             <Row>
             <Col md="6" className="marginRow justify marginRow_bottom">
                <h5 className="bold">SERVICIO SOCIAL</h5>
                <p>El servicio social es una de las condiciones que debes cumplir semestralmente para asegurar tu permanencia en el programa, conoce aquí las características: </p>
                <p><span className="bold">Cantidad de horas: </span>la cantidad de horas que debe realizar cada estudiante semestralmente pueden ser 50, 80 o 100 horas de servicio social, varían de acuerdo con el valor que cubre el municipio por concepto de matrícula</p>

                <p>
                  <ul className="list-fixed">
                  <li><span>Si el valor es inferior a 1 SMMLV, entonces debe cumplir con 50 horas.  </span></li>
                  <li><span>Si el valor es superior a 1 SMMLV e inferior a 2SMMLV, entonces debe cumplir con 80 horas. </span></li>
                  <li><span>Si el valor es superior a 2 SMMLV, entonces debe cumplir con 100 horas. </span></li>

                  </ul>
                </p>
                <p><span className="bold">Características de las instituciones:  </span>la prestación de este servicio social debe realizarse en dependencias de la administración municipal, se aceptan las siguientes: </p>
                <p>
                  <ul className="list-fixed">
                  <li><span>Instituciones Educativas oficiales. </span></li>
                  <li><span>Secretarias de despacho. </span></li>
                  <li><span>Entes descentralizados. </span></li>
                  </ul>
                </p>
                <p><span className="bold">Formato de seguimiento:  </span>la prestación de este servicio social debe ser certificado y firmado en el formato establecido por el programa.</p>
                <p><a rel="noopener noreferrer" href="https://cidpullzonestorage.b-cdn.net/supere/pdf/FormatoServicioSocial20182.pdf" target="_blank"><Button color="primary">Descarga aquí el formato vigente para este periodo. </Button></a></p>
                </Col>
                <Col md="6" className="marginRow marginRow_bottom"><img src="https://cidpullzonestorage.b-cdn.net/supere/serviciosocial.jpg" alt=""/></Col>
             </Row>
          </TabPane>
        </TabContent>

           <TabContent activeTab={this.state.activeTab}>
          <TabPane tabId="4">
             <Row>
             <Col md="6" className="marginRow justify marginRow_bottom">
                <h5 className="bold">ACOMPAÑAMIENTO PSICOSOCIAL</h5>
                    <p>Este espacio se ha creado pensando en las necesidades que presentan los estudiantes en su proceso de adaptación a la universidad; a través de este, buscamos ofrecerles un acompañamiento continuo por parte nuestro psicólogo, para tratar y discutir diferentes temas que pueden afectar sus procesos de formación académica. </p>
                <p>Por medio de este acompañamiento, los estudiantes pueden discutir temáticas como: </p>

                <p>
                  <ul className="list-fixed">
                  <li><span>Problemas o situaciones familiares. </span></li>
                  <li><span>Manejo de estrés. </span></li>
                  <li><span>Técnicas de estudio. </span></li>
                  <li><span>Trabajo bajo presión. </span></li>

                  </ul>
                </p>
                <p>Si deseas acceder a este servicio, debes enviar un correo a <b>superenvigado@gmail.com</b>, solicitando una cita para tu primera
                  sesión, en donde especifiques los siguientes datos, la cita te será asignada y recibirás una confirmación a través del correo.</p>
                <p>
                  <ul className="list-fixed">
                  <li><span>Nombre Completo</span></li>
                  <li><span>Carrera</span></li>
                  <li><span>Universidad</span></li>
                  <li><span>Motivo de consulta</span></li>
                   <li><span>Horario de disponibilidad</span></li>
                  </ul>
                </p>
                </Col>
                <Col md="6" className="marginRow marginRow_bottom"><img src="https://cidpullzonestorage.b-cdn.net/supere/contexto4.jpg" alt=""/></Col>
             </Row>
          </TabPane>
        </TabContent>

           <TabContent activeTab={this.state.activeTab}>
          <TabPane tabId="5">
             <Row>
             <Col md="6" className="marginRow justify ">
                <h5 className="bold">INGLÉS SUPERÉ</h5>
                <p>Esta es una herramienta de diagnóstico, diseñada por el programa para que sus estudiantes se nivelen en esta segunda lengua; pretende poner a su disposición una serie de cursos virtuales, en donde puedan probar sus conocimientos y habilidades. Actualmente tiene un cubrimiento hasta el nivel B1 y no es certificable.  </p>

                <p>Si deseas acceder a este servicio, debes enviar un correo a <b>superenvigado@gmail.com</b>, solicitando una cita para tu primera
                  sesión, en donde especifiques los siguientes datos, la cita te será asignada y recibirás una confirmación a través del correo.</p>

                </Col>

                <Col md="6" className="marginRow "><img src="https://cidpullzonestorage.b-cdn.net/supere/contexto5.jpg" alt=""/></Col>
                <Col md="12" className="center marginRow marginRow_bottom"> <a rel="noopener noreferrer" href="http://inglessupere.cid.edu.co" target="_blank"><Button color="primary">VISITA INGLÉS SUPERÉ</Button></a></Col>
             </Row>
          </TabPane>
        </TabContent>

     </Row>
    );
  }
}

export default Home;



