import React from 'react';
import {  Row, Col } from 'reactstrap';
import BigCalendar from 'react-big-calendar';
import moment from 'moment';
import 'moment/locale/es'
import MetaTags from 'react-meta-tags';
import "react-big-calendar/lib/css/react-big-calendar.css";

BigCalendar.momentLocalizer(moment);
const messages = {
  august :'agosto',
  allDay: 'Todos los dias',
  previous: 'Anterior',
  next: 'Siguiente',
  today: 'Hoy',
  month: 'Mes',
  week: 'Semana',
  day: 'Dia',
  agenda: 'Agenda',
  date: 'Fecha',
  time: 'Hora',
  event: 'Evento', // Or anything you want
  showMore: total => `+ ${total} événement(s) supplémentaire(s)`

}


class Cronograma extends React.Component {

    componentDidMount() {
      window.scrollTo(0, 0)
    }

   state = {
    events: [
      {
        start: 'Fri Aug 15 2018 13:29:58 GMT-0500 (hora estándar de Colombia)',
        end: 'Fri Oct 15 2018 13:29:58 GMT-0500 (hora estándar de Colombia)',
        title: "INSCRIPCIONES"
      },
      {
        start: 'Fri Oct 1 2018 13:29:58 GMT-0500 (hora estándar de Colombia)',
        end: 'Fri Oct 30 2018 13:29:58 GMT-0500 (hora estándar de Colombia)',
        title: "PRESENTACIÓN DE PRUEBAS"
      },
       {
        start: 'Fri Nov 1 2018 13:29:58 GMT-0500 (hora estándar de Colombia)',
        end: 'Fri Nov 5 2018 13:29:58 GMT-0500 (hora estándar de Colombia)',
        title: "REVISIÓN DOCUMENTAL"
      },
       {
        start: 'Fri Nov 13 2018 13:29:58 GMT-0500 (hora estándar de Colombia)',
        end: 'Fri Nov 13 2018 13:29:58 GMT-0500 (hora estándar de Colombia)',
        title: "DIVULGACIÓN DE RESULTADOS"
      }
    ]
  };

  render() {
    return (
    <Row className="align_center">
      <MetaTags>
        <title>Cronograma Superé| Superé Envigado</title>
        <meta property="og:title" content="Cronograma Superé" />
      </MetaTags>
    <Col md="4" className="marginRow marginRow_bottom justify">
     <h5 className="bold">CONSULTORIO MATEMÁTICO</h5>
     <p>
     <p>
       <h6 className="bold_green">INSCRIPCIONES:</h6>
       <p>15 agosto – 15 octubre</p>
     </p>
      <p>
       <h6 className="bold_green">PRESENTACIÓN DE PRUEBAS:</h6>
       <p>1 octubre – 30 octubre.</p>
     </p>
      <p>
       <h6 className="bold_green">REVISIÓN DOCUMENTAL:</h6>
       <p>1 noviembre – 5 noviembre.</p>
     </p>
     <p>
       <h6 className="bold_green">DIVULGACIÓN DE RESULTADOS:</h6>
       <p>13 noviembre.</p>
     </p>
     </p>
    </Col>
    <Col md="8" className="marginRow marginRow_bottom">
        <BigCalendar
        culture='es-ES'
          messages={messages}
          defaultDate={new Date()}
          defaultView="month"
          events={this.state.events}
          style={{ height: "100vh" }}
        />
          </Col>


    </Row>

    );
  }
}

export default Cronograma;



