import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import MetaTags from 'react-meta-tags';
class Requisitos extends React.Component{
  componentDidMount() {
    window.scrollTo(0, 0)
  }
  render(){
  return(
    <div className="cotenido_general">
      <MetaTags>
        <title>Requisitos de Superé| Superé Envigado</title>
        <meta property="og:title" content="Requisitos de Superé" />
      </MetaTags>
      <Container>
        <Row>
          <Col xs="12" className='tittle'><h2 className='tittleh'>Requisitos</h2></Col>
          <Col xs="12" className='tittle'><div className='sublineP' div></div></Col>
          <Col xs="12" md='6' className='marginRow'>
            <div className='imgB'>
             <img src="https://cidpullzonestorage.b-cdn.net/supere/06REQUISITOS.png" alt=""/>
            </div>
          </Col>
          <Col xs="12" md='6' className='marginRow'>
            <p>
              Aquellos estudiantes que estén interesados en presentarse
              a Superé, deben cumplir con la totalidad de
              los siguientes requisitos:
            </p>
            <ul className='list-fixed'>
              <li>
                <span>Ser residente del Municipio de Envigado hace
                  más de 5 años. </span>
              </li>
              <li>
                <span>Ser graduado de un colegio (público o privado)
                  de Envigado, del 2010 en adelante. </span>
              </li>
              <li>
                <span> Haber obtenido un promedio académico en el
                  grado 11, mínimo de 3,5. </span>
              </li>
              <li>
                <span> Estar inscrito en el SISBEN del municipio de
                  Envigado. </span>
              </li>
              <li>
                <span> Estar entre los estratos socio – económicos 1, 2,
                  3 o 4 (casos especiales del 5 y 6). </span>
              </li>
              <li>
                <span>  Comenzar la universidad desde el primer semestre.</span>
              </li>
              <li>
                <span>  El presente beneficio se aplica a un estudiante por familia, no aplicará para un segundo miembro de la familia hasta en un vínculo de segundo grado de consanguinidad.</span>
              </li>
              <li>
                <span>  No podrán recibir ninguno de los beneficios enunciados en este acuerdo los hijos de los empleados adscritos a la Administración Municipal.</span>
              </li>
            </ul>
          </Col>
        </Row>
        <Row className='marginRow'>
          <Col xs="12" md='7' className='marginTB'>
            <p>
              Si quieres presentarte a nuestras convocatorias,
              comienza a reunir la siguiente documentación:
            </p>
            <ul className='list-fixed'>
              <li>
                <span>Copia del documento de identidad ampliado al 150. </span>
              </li>
              <li>
                <span>Copia del certificado de puntaje de SISBEN. </span>
              </li>
              <li>
                <span>Copia de la última cuenta de servicios donde se vea el estrato. </span>
              </li>
              <li>
                <span> Copia del resultado del ICFES.  </span>
              </li>
              <li>
                <span> Copia del certificado de promedio o desempeño académico del grado 11.  </span>
              </li>
            </ul>
          </Col>
          <Col xs="12" md='5' className='marginTB marginRow_bottom'>
            <div className='imgB'>
              <img src='https://cidpullzonestorage.b-cdn.net/supere/4DOCUMMENTACIÓN.png'alt="" />
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  )}
}

export default Requisitos;



