import React from 'react';
import {Collapse,Navbar,NavbarToggler,NavbarBrand,Nav,NavItem } from 'reactstrap';
import { NavLink} from 'react-router-dom';
 class Header extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    return (
      <div>
        <Navbar expand="lg" light>
          <NavbarBrand href="/"><img src="https://cidpullzonestorage.b-cdn.net/supere/1LOGOSUPERE.png" alt=""/></NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem activeClassName="active" >
                <NavLink exact to="/">INICIO</NavLink>
              </NavItem>
               <NavItem activeClassName="active" >
                <NavLink to="/Como_funciona">¿CÓMO FUNCIONA?</NavLink>
              </NavItem>
               <NavItem activeClassName="active" >
                <NavLink to="/Donde_estudiar">QUÉ Y DÓNDE ESTUDIAR</NavLink>
              </NavItem>
               <NavItem activeClassName="active" >
                <NavLink to="/Requisitos">REQUISITOS</NavLink>
              </NavItem>
               <NavItem activeClassName="active" >
                <NavLink to="/Preguntas">PREGUNTAS</NavLink>
              </NavItem>
               <NavItem className="inscribite">
                <NavLink to="/Inscribite">INSCRÍBETE</NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}

export default Header;



