import React, { Component } from 'react';
import '../node_modules/font-awesome/css/font-awesome.min.css';
import Header from './components/Header.js';
import Footer from './components/Footer.js';
import Home from './components/Home.js';
import Como_funciona from './components/Como_funciona.js';
import Donde_estudiar from './components/Donde_estudiar.js';
import Requisitos from './components/Requisitos.js';
import Cronograma from './components/Cronograma.js';
import Preguntas from './components/Preguntas.js';
import Inscribite from './components/Inscribite.js';
import Slider from './components/Slider.js';
import NoMatch from './components/404.js';
import { Container, Row, Col } from 'reactstrap';
import { Route, BrowserRouter as Router, Switch} from 'react-router-dom';
import './App.css';
class App extends Component {
  render() {
    return (
       <Router>
       <div className="website">
         <Header />
         <Slider />
            <Container>
            <Row>
            <Col md="12">
            <Switch>
              <Route path='/Como_funciona' component={Como_funciona} />
              <Route  path='/Donde_estudiar' component={Donde_estudiar} />
              <Route  path='/Requisitos' component={Requisitos} />
              <Route  path='/Preguntas' component={Preguntas} />
              <Route  path='/Inscribite' component={Inscribite} />
              <Route exact path='/' component={Home} />
              <Route component={NoMatch} />
            </Switch>
            </Col>
            </Row>
            </Container>
          <Footer />
        </div>
      </Router>
    );
  }
}

export default App;



