import React from 'react';
import {  Modal, ModalHeader, ModalBody } from 'reactstrap';

class PopUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false
    };

    this.toggle = this.toggle.bind(this);
  }

  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }

  render() {
    return (
      <div className="Pop">
      <img src={this.props.image} className="infografico" onClick={this.toggle} alt=""/>
      <small className="ImgText">Haz clic en la imagen para ampliarla.</small>
        <Modal isOpen={this.state.modal} size="lg" toggle={this.toggle} className={this.props.className}>
          <ModalHeader toggle={this.toggle}></ModalHeader>
          <ModalBody>
            <img src={this.props.image} alt=""/>
          </ModalBody>
        </Modal>
      </div>
    );
  }
}

export default PopUp;



